html, body {
  height: 100%; }

body {
  font-size: 16px; }

h1, h2, h3, h4, h5, h6 {
  font-weight: 700;
  margin: 0; }

svg:not(:root) {
  overflow: hidden; }

ul {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-bottom: 1rem; }

a {
  word-wrap: break-word;
  color: #006ff9;
  -webkit-tap-highlight-color: transparent; }
  a:hover {
    text-decoration: none; }

input {
  border: none; }

p {
  margin: 0;
  margin-bottom: 1rem;
  line-height: 1.5; }

hr {
  width: 100%;
  margin-top: 2rem;
  margin-bottom: 2rem;
  border: 0;
  border-top: 1px solid #eee; }

img {
  vertical-align: middle;
  border-style: none;
  max-width: 100%; }

.icon {
  display: inline-block;
  line-height: 0.8; }
  .icon > svg {
    display: inline-block;
    font-size: inherit;
    width: 1em;
    height: 1em;
    overflow: visible;
    stroke-width: 0;
    stroke: currentColor;
    fill: currentColor; }

.icons {
  display: none; }

.site-slide {
  height: auto;
  overflow-x: hidden; }
  .site-slide .site__body {
    overflow-x: hidden; }
  .site-slide.open-slide {
    overflow-y: hidden; }

